.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container-override{
	text-align: center;
	/*background: radial-gradient(circle, rgba(56,195,204,1) 0%, rgba(0,212,255,1) 100%);*/
	height: 100vh;
	font-family: 'Comfortaa';
}

.dontRead {
	speak: none;
}

body,html{
  background-color: #F0EDF5;
  line-height: 2.5em;
}

a {
  color: #0400ff;
}
